import './style.css';
import generateMath from '../../tasks/arithmetic/index';
import generateTranslate from '../../tasks/translate/index';
import Button from '../../buttons/Button/Button';
import random from '../../randomizer/index';

export default class TaskModal {
  constructor() {
    this.modal = document.createElement('div');
    this.modal.classList.add('task-modal');
    const checkBtn = new Button('Check Result', 'check-btn');
    this.btn = checkBtn.createButton();
    this.tasks = [generateMath(), generateTranslate()]; 
    this.chosenTask = random(this.tasks);
    const { task, res, answer } = this.chosenTask;
    this.task = task;
    this.answer = answer;
    this.res = res;
    this.correct = res[res.length - 1];
    this.isCorrect = this.isCorrect.bind(this);
  }
   
  render() {
    this.modal.appendChild(this.task);
    this.modal.appendChild(this.btn);
    document.body.appendChild(this.modal);
    this.answer.focus();
  }

  remove() {
    this.modal.remove();
    return null;
  }

  isCorrect() {
    switch (this.chosenTask.type) {
      case 'math' :
        return this.correct == this.answer.value;
      case 'translate' :
        return this.res.includes(this.answer.value);
    }
  }

  correctColor() {
    this.modal.classList.add('correct');
  }

  failColor() {
    this.modal.classList.add('fail');
  }
}