import random from '../randomizer/index';
import game_background_1 from './img/game_background_1.png'
import game_background_2 from './img/game_background_2.png'
import game_background_3 from './img/game_background_3.png'
import game_background_4 from './img/game_background_4.png'

export default function randomBackground() {
	const url = [game_background_1, game_background_2, game_background_3, game_background_4];
	let back = random(url);
	document.querySelector('.battle-wrapper').style.background = `url(${back}) no-repeat center 80%`;
}